body {
    background: #4a4744 !important;
  }
  
  .wrapper {
    margin-top: 80px;
    margin-bottom: 80px;
  }
  
  .form-signin {
    max-width: 380px;
    padding: 15px 35px 45px;
    margin: 0 auto;
    background-color: white;
    border-top: 5px solid black;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    -webkit-box-shadow: 3px 3px;
            box-shadow: 3px 3px;
  }
  
  .checkbox {
    margin-bottom: 30px;
  }
  
  .form-control {
    position: relative;
    font-size: 16px;
    height: auto;
    padding: 10px;
    z-index: 2;
  }
  
  .form-signin-heading {
    margin-bottom: 20px;
  }
  
  input[type="text"] {
    margin-bottom: 5px;
  }
  
  input[type="password"] {
    margin-bottom: 20px;
  }
  
  .either-or {
    margin-top: 5px;
  }
  
  .hor-line1 {
    width: auto;
    border-bottom: 2px #4a4744 solid;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  
  .hor-line2 {
    width: auto;
    border-bottom: 2px #4a4744 solid;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  
  
 


